@font-face {
    font-family: 'TTOctosquares';
    src: url('TTOctosquares-Light.eot');
    src: url('TTOctosquares-Light.eot?#iefix') format('embedded-opentype'),
        url('TTOctosquares-Light.woff2') format('woff2'),
        url('TTOctosquares-Light.woff') format('woff'),
        url('TTOctosquares-Light.ttf') format('truetype'),
        url('TTOctosquares-Light.svg#TTOctosquares-Light') format('svg');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

